.timer {
  margin-top: 10px;
  display: flex;
  justify-content: left;
}

.field {
  font-size: 20px;
  font-weight: 300;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}
.field:not(:last-child) {
  margin-right: 30px;
}

.value {
  font-size: 40px;
  justify-content: center;
  font-weight: 500;
}
